@font-face {
  font-family: "IranSans";
  src: url("./Assets/Font/IRANSansWeb(FaNum).ttf") format("ttf"),
    url("./Assets/Font/IRANSansWeb(FaNum).woff") format("woff"),
    url("./Assets/Font/IRANSansWeb(FaNum).woff2") format("woff2"),
    url("./Assets/Font/IRANSansWeb(FaNum).eot") format("eot");
}

:root {
  --IranSansWeb: IranSans;
}

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: white;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  direction: rtl;
  scroll-behavior: smooth;
  font-family: "IranSans";
}

a:hover {
  cursor: pointer;
}
a:active {
  transform: scale(0.9);
}

.Search-Modal-Result-Box::-webkit-scrollbar {
  width: 5px;
}
.Search-Modal-Result-Box::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 10px;
}
.Search-Modal-Result-Box::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 10px;
}
.Search-Modal-Result-Box::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.Custom-Select-Box-Drop-Down::-webkit-scrollbar {
  display: none;
}
.Custom-Select-Box-Drop-Down::-webkit-scrollbar-track {
  background: #f1f1f1;
}
.Custom-Select-Box-Drop-Down::-webkit-scrollbar-thumb {
  background: #888;

  &:hover {
    background: #555;
  }
}

/////////////////////////////////////////////////////////

$scrollbar-width: 12px;
$track-bg: #1e1e2f;
$thumb-bg: linear-gradient(135deg, #ff6f61, #6b48ff);
$thumb-hover-bg: linear-gradient(135deg, #ff8a80, #8a63ff);
$glow-color: rgba(255, 111, 97, 0.5);

@keyframes glow-pulse {
  0% {
    box-shadow: 0 0 10px $glow-color;
  }
  50% {
    box-shadow: 0 0 20px $glow-color, 0 0 30px $glow-color;
  }
  100% {
    box-shadow: 0 0 10px $glow-color;
  }
}

